import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Fab from '@material-ui/core/Fab';
import { Send } from '@material-ui/icons';
import { makeStyles } from '@material-ui/core/styles';
import teal from '@material-ui/core/colors/teal';

const useStyles = makeStyles((theme) => ({
  instructorCard: {
    borderLeft: `3px solid #14213d`,
  },
  avatar: {
    border: `1px solid ${theme.palette.grey[500]}`,
  },
}));
// Source: https://codesandbox.io/s/r0wo8094p?file=/src/Component.js
export default function ProjectCard01(props) {
  const { instructor } = props;
  const classes = useStyles();
  const cardClassName = instructor
    ? `${classes.instructorCard} MuiProjectCard--01`
    : 'MuiProjectCard--01';
  return (
    <Card className={cardClassName}>
      <div className={'MuiCard__head'}>
        <Avatar
          style={{
            marginLeft: 10,
          }}
          className={'MuiAvatar-root ' + classes.avatar}
          src={props.img}
        />

        <Typography
          variant={'subtitle1'}
          gutterBottom
          style={{ paddingRight: 10, textAlign: 'right', paddingTop: 5 }}
        >
          {props.name}
        </Typography>
      </div>

      <Fab
        color={'secondary'}
        style={{ width: 35, height: 10, marginLeft: 50}}
      >
        <Send style={{ width: 20, height: 20}} />
      </Fab>
      <CardContent className={'MuiCardContent-root'}>
        <Typography style={{ paddingLeft: 10 }} variant={'subtitle2'}>
          @{props.username}
        </Typography>
        <Typography
          style={{ paddingLeft: 10 }}
          variant={'subtitle2'}
          gutterBottom
        >
          Country: {props.country}
        </Typography>
      </CardContent>
    </Card>
  );
}

ProjectCard01.getTheme = (muiBaseTheme) => ({
  MuiCard: {
    root: {
      '&.MuiProjectCard--01': {
        transition: '0.3s',
        margin: 'auto',

        '& .MuiCard__head': {
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          '& .MuiAvatar-root': {
            width: 60,
            height: 60,
            backgroundColor: '#ffffff',
            transform: 'translateY(50%)',
          },
        },
        '& .MuiDivider-root': {
          marginLeft: -muiBaseTheme.spacing.unit * 3,
          marginRight: -muiBaseTheme.spacing.unit * 3,
        },
        '& .MuiCardContent-root': {
          textAlign: 'left',
          padding: 0,
          paddingTop: muiBaseTheme.spacing.unit,
        },
      },
    },
  },
});
