import React from 'react';
import {
  Link as RouterLink,
  Switch,
  Route,
  useRouteMatch,
} from 'react-router-dom';
import { Typography, Breadcrumbs, Link } from '@material-ui/core';
import UnitOverview from './UnitOverview';
import MainLayout from '../components/layout/MainLayout';
import UnitCard from '../components/UnitCard';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  title: {
    marginBottom: theme.spacing(3),
  },
}));

const sideBarContent = (
  <>
    <Typography paragraph>
      Students can revisit the content of previous units anytime, but they can
      no longer add or change submissions.
    </Typography>

    <Typography paragraph>
      Since it is almost the end of the current unit, students can already
      preview the first content that is in the upcoming unit.
    </Typography>

    <Typography paragraph>
      In the prototype only the page for the current unit is implemented. The
      other pages would look similar.
    </Typography>
  </>
);

function AllUnits() {
  const classes = useStyles();
  return (
    <MainLayout sideBarContent={sideBarContent}>
      <Breadcrumbs aria-label="breadcrumb">
        <Link component={RouterLink} to="/">
          Home
        </Link>
        <Typography color="textPrimary">Units</Typography>
      </Breadcrumbs>
      <Typography variant="h3" className={classes.title}>Units</Typography>
      {/* <Typography paragraph>We are currently in unit 3 out of 12.</Typography> */}
      <UnitCard
        title="Unit 1: News and Media"
        date={'05.05. – 18.05'}
        completed={true}
        link="/units"
      >
        News and media play an important role in our lives. They are also
        powerful tools that can be used to spread misinformation. In this unit
        we will critically examine the role of news and media. We will also take
        a special look at social media.
      </UnitCard>
      <UnitCard
        title="Unit 2: Echo Chambers and Filter Bubbles"
        date={'19.05. – 01.06.'}
        completed={true}
        link="/units"
      >
        When people who already have the same viewpoints keep discussing, this
        can lead to radicalization. Additionally the algorithms of Facebook,
        Google and others tend to show us things that are similar to what we
        have liked in the past. In this unit we will examine the negative
        effects of Filter Bubbles and Echo Chambers.
      </UnitCard>
      <UnitCard
        title="Unit 3: Cognitive Bias"
        date={'02.06. – 15.06.'}
        link="/units/current"
        current={true}
      >
        A cognitive bias is a systematic pattern of deviation from norm or
        rationality in judgment. In this unit we learn how cognitive biases can
        influece our perception and behaviour. We will also examine how
        politicians and media exploit biases.
      </UnitCard>
      <UnitCard
        title="Unit 4: Algorithmic Bias"
        date={'16.06. – 30.06.'}
        link="/units"
        upcoming={true}
      >
        Computers can help us in many ways. But we often overestimate how smart
        they are. As there is a lot of bias present in our world and the poeple
        who create computers and computer programs, often computer systems are
        biased, too. If you feed a computer a racist dataset, it will learn to
        make racist decisions. In this unit we will learn how to spot different
        forms of bias in algorithms and computer systems and what to do about
        it.
      </UnitCard>
    </MainLayout>
  );
}

export default function UnitList() {
  let match = useRouteMatch();
  return (
    <Switch>
      <Route path={`${match.path}/:unitId`}>
        <UnitOverview />
      </Route>
      <Route path={match.path}>
        <AllUnits />
      </Route>
    </Switch>
  );
}
