import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Card, CardContent, Button, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import FastForwardIcon from '@material-ui/icons/FastForward';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';

const useStyles = makeStyles((theme) => ({
  card: {
    marginBottom: theme.spacing(3),
    backgroundColor: '#E5E5E5',
  },
  cardCurrent: {
    marginBottom: theme.spacing(3),
  },
  iconAndText: {
    display: 'flex',
  },
  iconStatus: {
    display: 'flex',
    float: 'right'
  },
  green: {
    color: theme.palette.success.main,
  },
  info: {
    color: theme.palette.info.main,
  },
  iconText: {
    marginLeft: theme.spacing(1),
  },
  title: {
    marginBottom: theme.spacing(1),
  },
  button: {
    marginTop: theme.spacing(2),
    float: 'right',
    marginBottom: theme.spacing(2)
  },
}));

export default function AssignmentCard(props) {
  const { title, date, completed, upcoming, children, link, current } = props;
  let buttonStyle, buttonText;
  if (completed) {
    buttonStyle = 'outlined';
    buttonText = 'Revisit Unit';
  } else if (upcoming) {
    buttonStyle = 'outlined';
    buttonText = 'Preview Unit';
  } else {
    buttonStyle = 'contained';
    buttonText = 'Go to Current Unit';
  }
  const classes = useStyles();
  return (
    <Card className={completed ? classes.card : classes.cardCurrent}>
      <CardContent>
        {completed && (
          <span className={classes.iconStatus + ' ' + classes.green}>
            <CheckCircleIcon fontSize="small" />
            <span className={classes.iconText}>completed</span>
          </span>
        )}
        {upcoming && (
          <span className={classes.iconStatus + ' ' + classes.info}>
            <FastForwardIcon fontSize="small" />
            <span className={classes.iconText}>upcoming</span>
          </span>
        )}
        {current && (
          <span className={classes.iconStatus }>
            <SlowMotionVideoIcon fontSize="small" />
            <span className={classes.iconText}>in progress</span>
          </span>
        )}
        <Typography variant="h5" className={classes.title}>
          {completed && (<span>{title}</span>)}
          {upcoming && (<span>{title}</span>)}
          {current && (<span><b>{title}</b></span>)}
        </Typography>
        {date && (
          <Typography gutterBottom>
            <span className={classes.iconAndText}>
              <CalendarTodayIcon fontSize="small" style={{ paddingTop: 2 }}/>
              {completed && (<span className={classes.iconText}>{date}</span>)}
              {upcoming && (<span className={classes.iconText}>{date}</span>)}
              {current && (<span className={classes.iconText}><b>{date}</b></span>)}
            </span>
          </Typography>
        )}
        <Typography>{children}</Typography>
        <Button
          className={classes.button}
          color="secondary"
          variant={buttonStyle}
          component={RouterLink}
          to={link}
        >
          {buttonText}
        </Button>
      </CardContent>
    </Card>
  );
}
