import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Card, CardContent, Button, Typography } from '@material-ui/core';
import NewReleasesIcon from '@material-ui/icons/NewReleases';
import { makeStyles } from '@material-ui/core/styles';
import ImportContactsIcon from '@material-ui/icons/ImportContacts';

const useStyles = makeStyles((theme) => ({
  card: {
    marginBottom: theme.spacing(3),
  },
  iconAndText: {
    display: 'flex',
  },
  iconComplete:{
    display: 'flex',
    alignItems: 'center',
    float: 'right'
  },
  info: {
    color: theme.palette.info.main,
  },
  iconText: {
    marginLeft: theme.spacing(1),
  },
  title: {
    marginBottom: theme.spacing(1),
  },
  button: {
    marginTop: theme.spacing(2),
    float: 'right',
    marginBottom: theme.spacing(2)
  },
}));

export default function AssignmentCard(props) {
  const { children, title, link, isNew } = props;
  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardContent>
        {isNew && (
          <span className={classes.iconComplete + ' ' + classes.info}>
            <NewReleasesIcon fontSize="small" />
            <span className={classes.iconText}>new</span>
          </span>
        )}
         <span className={classes.iconAndText}>
            <ImportContactsIcon style={{ paddingTop: 4 }}/>
        <span className={classes.iconText}><Typography variant="h5" className={classes.title}>
          {title}
        </Typography>
        </span>
        </span>
        {children}
        <Button
          className={classes.button}
          color="secondary"
          variant="contained"
          component={RouterLink}
          to={link}
        >
          View material
        </Button>
      </CardContent>
    </Card>
  );
}
