import React from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Card, CardContent, Button, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import AssignmentIcon from '@material-ui/icons/Assignment';
import Grid from '@material-ui/core/Grid';
import SlowMotionVideoIcon from '@material-ui/icons/SlowMotionVideo';

const useStyles = makeStyles((theme) => ({
  card: {
    marginBottom: theme.spacing(3),
  },
  cardCompleted: {
    marginBottom: theme.spacing(3),
    backgroundColor: '#E5E5E5',
  },
  iconAndText: {
    display: 'flex',
  },
  iconComplete:{
    display: 'flex',
    alignItems: 'center',
    float: 'right'
  },
  green: {
    color: theme.palette.success.main,
  },
  iconText: {
    marginLeft: theme.spacing(1),
  },
  title: {
    marginBottom: theme.spacing(1),
  },
  titleBold:{
    marginBottom: theme.spacing(1),
    fontWeight: 'bold',
  },
  grid: {
    marginTop: theme.spacing(2),
    //marginBottom: theme.spacing(2)
  },
  button: {
    float: 'right',
  },
}));

export default function AssignmentCard(props) {
  const { title, due, completed, current, children, link } = props;
  const classes = useStyles();
  return (
    <Card className={completed ? classes.cardCompleted : classes.card}>
      <CardContent>
        {completed && (
          <span className={classes.iconComplete + ' ' + classes.green}>
            <CheckCircleIcon fontSize="small" />
            <span className={classes.iconText}>completed</span>
          </span>
        )}
        {current && (
          <span className={classes.iconComplete}>
            <SlowMotionVideoIcon fontSize="small" />
            <span className={classes.iconText}>current</span>
          </span>
        )}
        <span className={classes.iconAndText}>
            <AssignmentIcon style={{ paddingTop: 4 }}/>
        <span className={classes.iconText}><Typography variant="h5" className={completed ? classes.title : classes.titleBold}>
          {title}
        </Typography>
        </span>
        </span>
        {children}
        <Grid container spacing={0} className={classes.grid}>
          <Grid item xs={6}>
          {due && (
          <Typography>
            <span className={classes.iconAndText}>
              <CalendarTodayIcon fontSize="small" style={{ paddingTop: 2 }}/>
              <span className={classes.iconText}><b>Due: {due}</b></span>
            </span>
          </Typography>
        )}
          </Grid>
          <Grid item xs={6}>
          <Button
          className={classes.button}
          color="secondary"
          variant="contained"
          component={RouterLink}
          to={link}
        >
          View assignment
        </Button>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}
